import {
	formatList
} from '../../../util/js/formatList.js';
const TITLE_HEIGHT = 23;
export default {
	data() {
		return {
			title: 'Hello',
			list: [],
			fixedTop: '',
			listHeight: [],
			height: 1000,
			currentIndex: 0,
			fixedTitle: '',
			scrollTop: 0,
			toNum: 0,
			name: '',
			id: '',
			keyWord: '',
		}
	},
	onLoad(options) {
		this.id = options.id;
		this.name = options.name;
		uni.setNavigationBarTitle({
			title: this.name
		})
		this.getList();
		if(this.list.length == 0){
			uni.showToast({
				icon: 'loading',
				title: '正在加载',
				duration: 500
			})
		}
	},
	mounted() {
		this._calculateHeight();
	},
	methods: {
		getList() {
			this.$http.get('/petbnb/encyclopedia/petTypeList', {
					petRaceId: this.id,
					keyWord: this.keyWord
				})
				.then(res => {
					// console.log(res.data.list);
					let dataList = res.data.list;
					let arr = formatList(dataList, 'firstWord');
					this.list = arr;
				})
				.catch(err => {
					uni.showToast({
						icon: 'none',
						title: '系统出错，请稍后再试!'
					})
				})
		},
		// 搜索
		confirm(e){
			this.keyWord = e.detail.value;
			this.getList();
		},
		scroll(e) {
			// console.log(e);
			let newY = e.detail.scrollTop;
			this.scrollY(newY);
		},
		scrollY(newY) {
			const listHeight = this.listHeight;
			// 当滚动到顶部，newY>0
			if (newY == 0 || newY < 0) {
				// this.currentIndex = 0;
				this.fixedTitle = '';
				return;
			}
			// 在中间部分滚动 
			for (let i = 0; i < listHeight.length - 1; i++) {
				let height1 = listHeight[i]
				let height2 = listHeight[i + 1]
				if (newY >= height1 && newY < height2) {
					this.fixedTitle = this.list[i].Title;
					this.fixedTt(height2 - newY);
					// this.currentIndex = i;
					return
				}
				// console.log(newY);
			}
			// 当滚动到底部，且-newY大于最后一个元素的上限
			this.currentIndex = this.listHeight.length - 2;
			this.fixedTitle = this.list[this.listHeight.length - 2].Title;
		},
		fixedTt(newVal) {
			let fixedTop = (newVal > 0 && newVal < TITLE_HEIGHT+52) ? newVal - TITLE_HEIGHT : 52;
			if (this.fixedTop === fixedTop) {
				return
			}
			this.fixedTop = fixedTop;
		},
		_calculateHeight() {
			let that = this;
			setTimeout(function() {
				let lHeight = [];
				let height = 0
				lHeight.push(height);
				let query = uni.createSelectorQuery().in(that);
				query.selectAll('.list-group').boundingClientRect(data => {
					// console.log(data);
					let rect = data,
						len = rect.length;
					for (let i = 0; i < len; i++) {
						height += rect[i].height;
						lHeight.push(height);
					}
					that.listHeight = lHeight;
				}).exec();
				// let calHeight = setInterval(function() {
				// 	if (lHeight != [0]) {
				// 		this.listHeight = lHeight
				// 		clearInterval(calHeight);
				// 	}
				// }, 0)
			}, 500)
		},
		// 点击侧边导航栏跳转
		scrollToview(e) {
			let id = e.target.dataset.id;
			// this.currentIndex = e.target.dataset.index;
			this.toNum = e.target.dataset.index;
		},
		// 进入详情
		enterDetail(id) {
			uni.navigateTo({
				url: './petDetail?id=' + id
			})
		}
	}
}
